/*全局样式*/
.container {
    display: flex;
    height: 100vh;
    overflow: hidden;
}


.container .one ul > li div h4, .container .two ul > li div h4 {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}

.one .list {
    cursor: pointer;
}

.active {
    border-radius: 8px;
    background-color: #ff9f62;
}

#activeFont {
    color: #fffef6;
}

#commonFont {
    color: #222222;
}

.container .two, .container .one {
    display: inline-block;
    height: 100vh;
    overflow-y: scroll;
    transition: width .1s linear;
    position: relative;
    margin-top: 15px;
}
.container .one {
    width: 15vw;
    padding: 20px;
    border-left: 1px solid #dddddd;
}

.container .two {
    width: 0;
    padding: 0;
}

.container .main {
    display: inline-block;
    width: 100%;
    height: 100vh;
    overflow-y: scroll;
    border-left: 1px solid #dddddd;
    padding: 20px;
    position: relative;
}

.title {
    cursor: pointer;
}

/*公共样式*/
.ant-spin-container .ant-list-items .ant-list-item {
    padding: 6px;
}

.ant-list .ant-spin-container .ant-list-item {
    padding: 3px;
}

.ant-list-item .ant-list-item-meta .ant-list-item-meta-content .ant-list-item-meta-title {
    padding: 0;
    margin: 0;
}

/*媒体查询*/
@media (max-width: 700px) {
    .container .one {
        display: inline-block;
        width: 0;
    }


    .container .two {
        /*flex: 1;*/
        display: inline-block;
        width: 0;
    }

    .container .main {
        /*flex: 1;*/
        display: inline-block;
        width: 100vw;
    }
}

/*图标*/

.layerIcon {
    position: absolute;
    left: 0;
    right: 0;
    top: 10px;
    display: flex;
}

.zoom {
    width: 30px;
    cursor: pointer;
    z-index: 100;
    position: sticky;
    top: 15px;
    left: 95vw;
    transition: all .3s linear;
}

.one_hide_show {
    width: 30px;
    cursor: pointer;
    z-index: 100;
    position: sticky;
    top: 15px;
    left: 10px;
    transition: all .3s linear;
}

.two_hide_show {
    width: 30px;
    cursor: pointer;
    z-index: 100;
    position: sticky;
    top: 15px;
    left: 45px;
    transition: all .3s linear;

}
